:host {
  flex: 1;
  display: flex;
}

.container {
  width: 100%;
  height: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.container-body {
  flex: 1;
  padding: 30px 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container-table{
  width: 100%;
  // height: 100%;
  background-color: inherit;
  border: 1px solid rgba($color: #000000, $alpha: .1);
}

.table-container{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #faf9fd;
  box-sizing: border-box;
  // padding: 10px;
}


.table-row{
  padding: 10px;
  box-sizing: border-box;
  background-color: white;
  cursor: pointer;
}

.mat-mdc-header-cell{
  border: 1px solid rgba($color: #000000, $alpha: .1);
  border-bottom-color: inherit !important;
}

.mat-mdc-cell{
  text-align: center;
  border: 1px solid rgba($color: #000000, $alpha: .1);
}

.spacer{
  flex: 1;
  background-color: white;
}
